html, body {
    height: 100%;
    margin: 0;
}

/* body {
    background-color: lightblue;
} */



#div_mainWrapper {
    display: flex;
    flex-flow: column;

    white-space: nowrap;
    overflow: hidden;

    height: 100%;

    /* background-color: lightgreen; */
}



#div_mainTitleBar {
    white-space: nowrap;
    overflow: hidden;

    height: 40px;

    position: relative;

    /* background-color: lightskyblue; */
}

#label_title {
    position: absolute;
    bottom: 0;
    left: 0;

    padding-left: 10px;

    font-family: Arial;
    font-size: 32px;
    font-weight: bold;
    color: rgb(52, 92, 156);
}

#label_version {
    position: absolute;
    bottom: 0;
    right: 0;

    padding-right: 10px;
    padding-bottom: 5px;

    font-family: Arial;
    font-size: 14px;
    font-weight: 100;
    color: grey;
}



#div_mainNavigationBar {
    white-space: nowrap;
    overflow: hidden;

    height: 40px;

    padding-left: 10px;
    padding-right: 10px;

    background-color: rgb(52, 92, 156);
}

.buttonClass_navigationButton {
    height: 100%;

    font-family: Arial;
    font-size: 16px;
    color: white;
    border: none;
    background-color: inherit;
}

.buttonClass_navigationButton:hover {
    background-color: rgb(102, 142, 206);
}

.buttonClass_navigationButton:active {
    background-color: rgb(27, 68, 131);
}

#button_animationControls {
    padding-left: 10px;
}



#div_mainContent {
    flex-grow: 1;

    white-space: nowrap;
    overflow: hidden;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 5px;

    /* background-color: lightgreen; */
}

.labelClass_columnHeader {
    font-family: Arial;
    font-size: 24px;
    font-weight: bold;
    color: rgb(52, 92, 156);
}

.labelClass_inputHeaderAnimation {
    padding-left: 20px;

    font-family: Arial;
    font-size: 14px;
    font-weight: bold;
}

.buttonClass_generalButton {
    height: 100%;

    font-family: Arial;
    font-size: 14px;
    border: 2px solid rgb(52, 92, 156);
    background-color: white;
}

.buttonClass_generalButton:hover {
    background-color: rgb(102, 142, 206);
    color: white;
}

.buttonClass_generalButton:active {
    background-color: rgb(52, 92, 156);
    /* border: 2px solid black; */
}

.inputClass_timeInput {
    width: 40px;

    box-sizing: border-box;

    height: 100%;

    padding-left: 5px;

    font-family: Arial;
    font-size: 14px;
    border: 2px solid lightgray;
    border-radius: 4px;
}



#div_columnLeft {
    display: flex;
    flex-flow: column;

    white-space: nowrap;
    overflow: hidden;

    float: left;
    height: 100%;
    width:60%;

    /* background-color: darkorange; */
}

#div_leftColumnHeader {
    white-space: nowrap;
    overflow: hidden;
    /* background-color: lawngreen; */
}

#div_animationControls {
    white-space: nowrap;
    overflow: hidden;

    height: 30px;

    /* background-color: lightgoldenrodyellow; */
}

.labelClass_checkboxHeaderAnimation {
    padding-left: 30px;

    font-family: Arial;
    font-size: 14px;
    font-weight: bold;
}

#div_animationStatus {
    white-space: nowrap;
    overflow: hidden;

    height: 30px;

    /* background-color: gray; */
}


#div_animationTimeBar {
    display: flex;
    flex-flow: row;
    position: relative;
    align-items: center;
    justify-content: space-evenly;
    
    white-space: nowrap;
    overflow: hidden;

    height: 30px;

    padding-top: 5px;

    /* background-color: red; */
}

#input_timeBar {
    -webkit-appearance: none;
    -webkit-transition: .2s;

    width: 92.5%;
    height: 15px;

    opacity: 0.75;
    transition: opacity .2s;
    border-radius: 5px;  
    background: #d3d3d3;
    outline: none;
}
  
#input_timeBar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: rgb(52, 92, 156);
    cursor: pointer;
    vertical-align: middle;
}
  
#input_timeBar::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: rgb(52, 92, 156);
    cursor: pointer;
    vertical-align: middle;
}

#label_animationTime {
    font-family: Arial;
    font-size: 15px;
}


#map {
    flex-grow: 1;

    padding-top: 5px;

    /* background-color: lightblue; */
}



#div_columnRight {
    display: flex;
    flex-flow: column;

    white-space: nowrap;
    overflow: hidden;

    float: right;
    height: 100%;
    width: 40%;

    /* background-color: darkred; */
}

#label_eventType {
    padding-left: 10px;
}

#div_rightColumnHeader {
    white-space: nowrap;
    overflow: hidden;

    padding-left: 10px;

    /* background-color: lawngreen; */
}

.labelClass_inputHeaderData {
    padding-left: 20px;

    font-family: Arial;
    font-size: 14px;
    font-weight: bold;
}

#div_dataSelection {
    /* display: flex; */
    white-space: nowrap;
    overflow: hidden;

    height: 30px;

    padding-left: 10px;

    /* background-color: lightcoral; */
}

.labelClass_selectHeader {
    font-family: Arial;
    font-size: 14px;
    font-weight: bold;
}

.selectClass_dataSelection {
    height: 100%;

    font-family: Arial;
    font-size: 14px;
}


#div_dataSelectionOtherOptions {
    white-space: nowrap;
    overflow: hidden;

    height: 30px;

    padding-top: 5px;
    padding-left: 10px;

    /* background-color: mediumblue; */
}

#label_previewEvent {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    padding-left: 25px;
}

#button_getEventData {
    float: right;
}

.labelClass_checkboxHeaderData {
    font-family: Arial;
    font-size: 14px;
    font-weight: bold;
}

#div_advancedDataSelect {
    display: none;
    overflow: hidden;

    white-space: nowrap;

    padding-top: 5px;
    padding-left: 10px;
    /* height: 50px; */
}

.inputClass_searchInput {
    box-sizing: border-box;
    
    padding-left: 5px;

    font-family: Arial;
    font-size: 14px;
    border: 2px solid lightgray;
    border-radius: 4px;
}

#div_topSearch {
    height: 23px;

    /* background-color: red; */
}

#input_vehicleIDSearch {
    float: left;

    width: 49.5%;
}

#input_dateSearch {
    float: right;

    width: 49.5%;
}

#div_bottomSearch {
    height: 23px;

    padding-top: 3px;

    /* background-color: blue; */
}

#input_timeFromSearch {
    float: left;

    width: 49.5%;
}

#input_timeToSearch {
    float: right;

    width: 49.5%;
}


#div_eventList {
    flex-grow: 1;

    white-space: nowrap;
    overflow: hidden;

    padding-top: 5px;
    padding-left: 10px;

    /* background-color: darkgoldenrod; */
}

#select_event {
    height: 100%;
    width: 100%;

    font-family: Arial;
    font-size: 13px;
}



#div_errorMessages {
    white-space: nowrap;
    overflow: hidden;

    height: 30px;
    position: relative;

    padding-left: 10px;
    padding-right: 10px;

    /* background-color: lightyellow; */
}

#label_errorMessages {
    font-family: Arial;
    font-size: 14px;
}